<script>
import { useStore } from "vuex";
import html2canvas from "html2canvas";
import { jsPDF } from "jspdf";
import { onMounted, reactive, ref } from "@vue/runtime-core";
import { useRouter } from "vue-router";
export default {
  setup() {
    let store = useStore();
    let router = useRouter();
    let color = ref("");
    let score = ref(null);
    let options = store.getters.getOptions;
    const userObj = reactive({
      email: "",
      company: "",
      name: "",
    });
    const count = {};
    let link = [
      "https://raw.githubusercontent.com/dmrt2002/images/696ad09814061b0a6d1ff1653ac680969870b760/heptagon.svg",
    ];
    for (const element of options) {
      if (count[element]) {
        count[element] += 1;
      } else {
        count[element] = 1;
      }
    }
    for (let i = 1; i <= 4; i++) {
      score.value = score.value + count[i] * i;
    }
    score.value = Math.ceil((score.value / 40) * 100);
    if (score.value < 25) {
      color.value = "Red";
    } else if (score.value < 50 && score.value > 25) {
      color.value = "Yellow";
    } else if (score.value < 75 && score.value > 50) {
      color.value = "Orange";
    } else if (score.value <= 100 && score.value > 75) {
      color.value = "Green";
    }
    const savePdf = () => {
      html2canvas(document.getElementById("report"), { allowTaint: true }).then(
        function (canvas) {
          canvas.getContext("3d");
          var pdf = new jsPDF("p", "mm", "a3");
          const imgData = canvas.toDataURL("image/png");
          const pdfWidth = pdf.internal.pageSize.getWidth() + 6;
          const pdfHeight = pdf.internal.pageSize.getHeight();
          console.log(pdfWidth);
          pdf.addImage(imgData, "JPEG", 0, 0, pdfWidth, pdfHeight);
          pdf.save("Report.pdf");
          router.push("/results");
        }
      );
    };
    onMounted(async () => {
      let user = store.getters.getUserObj;
      userObj.email = user.email;
      userObj.company = user.company;
      userObj.name = user.name;
      setTimeout(savePdf, 1000);
    });
    return { link, savePdf, userObj, score, color };
  },
};
</script>

